<template>
  <div id="line-trend-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        color: ["rgba(0,190,170,1)", "rgba(rgba(66,119,235,1))"],
        grid: {
          top: "8%",
          left: "1%",
          right: "4%",
          bottom: "9%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: ["1月", "3月", "6月", "9月", "12月"],
            axisTick: {
              show: false,
            },
            axisLabel: {
              color: "#fff",
            },
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.1)",
              },
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255,255,255,0.1)"],
                width: 1,
                type: "solid",
              },
            },
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        series: [
          {
            name: "Email",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(0,190,170,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(0,190,170,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [220, 182, 134, 234, 290, 132, 310],
          },
          {
            name: "Union Ads",
            type: "line",
            stack: "Total",
            symbol: "none",
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(66,119,235,0.1)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(66,119,235,1)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 220, 101, 134, 191, 0, 0],
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const lineTrendChart = document.querySelector("#line-trend-charts");
      const myChart = echarts.init(lineTrendChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#line-trend-charts {
  width: 100%;
  height: 220px;
}
</style>